import { Component, OnInit, Input } from '@angular/core';
import { Prof } from '../prof';
import { ProfService } from '../prof.service';
import { CourseService } from '../../courses/course.service';
import { Course } from '../../courses/course';

@Component({
  selector: 'app-prof-detail',
  templateUrl: './prof-detail.component.html',
  styleUrls: ['./prof-detail.component.css']
})
export class ProfDetailComponent implements OnInit {
  @Input() prof: Prof;
  forIn: number[];
  courses: Course[];
  constructor(private profService: ProfService, private courseService: CourseService) {
    this.forIn = []
    for (var i = 0; i < 30; i++)
      this.forIn.push(i);
  }

  ngOnInit() {
    this.courses = this.courseService.getCourses();
  }

  toggle(id: number) {
    if (this.prof.isAvailable[id] === true)
      this.prof.isAvailable[id] = false;
    else
      this.prof.isAvailable[id] = true;
  }
}



